@import "./variables.scss";

//**login dummy //**

.box-layout {
  width: 340px;
  margin: 3.2rem auto;
  font-size: 1.2rem;

  form {
    margin-bottom: 1.2rem;
    background: #f7f7f7;
    box-shadow: 0px 2px 2px rgba(0, 0, 0, 0.3);
    padding: 1.6rem;
  }

  h2 {
    margin: 0 0 1.2rem;
  }
}

a.link-tag {
  color: #ffffff;

  &:hover {
    color: #ffffff;
    text-decoration: none;
  }
}
//** ends**//

/**----- Login ------**/

.main-container {
  width: 340px;
  margin: 3.2rem auto;
  .container-wrapper {
    margin-bottom: 1.2rem;
    background: #f7f7f7;
    box-shadow: 0px 2px 2px rgba(0, 0, 0, 0.3);
    padding: 1.6rem;
  }
}
.login-container {
  height: 300px;
  position: relative;
}

.button-underline {
  color: #007bff;
}

.button-underline:hover {
  text-decoration: underline;
  color: #0056b3;
}

.sign-up-link {
  text-decoration: underline;
  color: $cornflowerblue-color;
}

.form-footer {
  border-top: 1px solid #dce8f1;
  padding: 11px;
  margin-top: 19px;
}

.font-icons {
  padding-right: 4px;
}

/*********** form validation css *******/
.form-control.is-invalid,
.was-validated .form-control:invalid {
  background-image: none !important;
}

.form-control.is-invalid,
.was-validated .form-control:invalid {
  background-image: none !important;
}

.form-control.is-valid,
.was-validated .form-control:valid {
  border-color: #ced4da !important;
  background-image: none !important;
}
.form-control.is-valid:focus,
.was-validated .form-control:valid:focus {
  background-image: none !important;
  border-color: #007bff !important;
}
.invalid-feedback {
  font-family: "EMPrintRegular" !important;
}

.form-control.is-invalid:focus,
.was-validated .form-control:invalid:focus {
  box-shadow: 0 0 0 0.2rem rgba(220, 53, 69, 0.25);
}

.form-control {
  //   border: 1px solid green;
  border-radius: 0rem;
}
.formInput {
  padding: 8px 9px;
  width: 100%;
}

.login-forms {
  padding-top: 20px;
}
.form-validation-invalid {
  font-size: 15px;
}

.api-error-message {
  color: red;
  font-weight: 600;
  margin-top: 18px;
}

.fa-arrow-alt-circle-left {
  color: #007bff;
}

.float-style {
  float: left;
}
