@import "./variables.scss";

/** --- User-list --- **/
.user-container {
  padding-left: 2%;
  padding-right: 2%;
  margin: 0 auto;
}
/** ---user details---**/
.user-details-text {
  padding-top: 10px;
  padding-bottom: 10px;
  font-weight: 600;
}

.user-profile-picture {
  border-radius: 90px;
  width: 180px;
  height: 180px;
}

.user-profile-container {
  height: 300px;
  position: relative;
  padding-left: 2%;
  padding-right: 2%;
  margin: 0 auto;
}

.user-profile-wrapper {
  margin: 0;
  position: absolute;
  top: 70%;
  left: 50%;
  -ms-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
  width: 300px;
}

.user-delete-link {
  // text-decoration: underline;
  // color: $cornflowerblue-color;
  // margin-bottom: 60px;
  margin-bottom: 22px;
}

/** -- Header -- **/
.user-header {
  //  background-color: rgba(255, 208, 0, 0.87);
  //background-image: linear-gradient(to right, #25c481, #25b7c4);
  background-color: #222930;
  height: 80px;
  border-bottom: 8px solid #4eb1ba;
}
.header-logout {
  float: right;
  cursor: pointer;
  font-weight: Bold;
}

.header-logout:hover {
  // text-decoration: underline;
  background-color: #4eb1ba;
  color: black;
  padding: 6px;
  border-radius: 5px;
}
.circle {
  width: 40px;
  height: 40px;
  border-radius: 25px;
  background-color: #4eb1ba;
  color: black;
  align-items: center;
  margin-left: 7%;
  padding-top: 6px;
}
.header-text {
  color: white;
  float: left;
  padding-top: 2px;
  font-size: 1.2rem;
  padding-left: 2%;
}
.header-logout-text {
  float: right;
  padding-right: 20px;
  font-weight: Bold;
  padding-top: 2px;
  font-size: 1.2rem;
  color: white;
}

.modal-body {
  font-weight: 500;
}

/*---Table Css----*/
.table-main-heading {
  //background-color: royalblue;
  background-color: #317379;
  color: white;
}

.table-email {
  text-decoration: underline;
}

.table-striped tbody tr:nth-of-type(odd) {
  //background-color: #0e62e63d;
  background-color: gba(0, 0, 0, 0.05);
}

.table-striped tbody tr:nth-of-type(even) {
  background-color: #0e62e61a;
}

.table-striped tbody tr:hover {
  //background-color: rgba(245, 180, 180, 0.1);
  background-color: rgba(14, 222, 255, 0.1);
}

@media screen and (min-width: 300px) and (max-width: 1000px) {
  .user-container {
    padding-left: 0%;
    padding-right: 0%;
    margin: 0 auto;
  }
  .user-profile-container {
    padding-left: 0% !important;
    padding-right: 0% !important;
    margin: 0 auto;
  }
  .circle {
    margin-left: 1%;
  }
  .header-text {
    padding-left: 1%;
  }
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}
